import { Pipe, PipeTransform } from '@angular/core';
import { IntlService } from './intl.service';
import { DateFormatOptions } from '@telerik/kendo-intl';

const isNumeric = (value: any): boolean => !isNaN(value - parseFloat(value));

/**
 * Formats a date value to a string based on the requested format.
 * This pipe uses the IntlService.
 * @see IntlService.
 *
 * @example <caption>Basic Usage</caption>
 * ```ng-template-no-run
 * <ul>
 *    <li>{{date | kendoDate }}</li>
 *    <li>{{milliseconds | kendoDate: 'M/dd/yyy' }}</li>
 *    <li>{{stringDate | kendoDate: 'G' }}</li>
 * </ul>
 * ```
 */
@Pipe({
    name: 'kendoDate'
})
export class DatePipe implements PipeTransform {

    constructor(private intlService: IntlService) { }

    public transform(value: any, format: String | DateFormatOptions = ""): any {
        value = this.normalize(value);

        if (value) {
            return this.intlService.formatDate(value, format);
        }
        return value;
    }

    private normalize(value: any): Date {
        if (value && typeof value === 'string') {
            value = this.intlService.parseDate(value);
        } else if (value && isNumeric(value)) {
            value = new Date(parseFloat(value));
        }
        return value;
    }
}



